@tailwind base;
@tailwind components;
@tailwind utilities;


@import url('https://fonts.googleapis.com/css2?family=Suwannaphum&display=swap');

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #0D1117;
    font-family: 'Roboto', sans-serif;
}


.header {
    background-color: black;
    border-bottom: 1px solid rgb(48, 54, 61);
    color: white;
    padding: 19px;
}

.content-container {
    display: flex;
    justify-content: space-between;
    margin: auto;
    width: 95%;
    height: auto;
    top: 10%;
}

.left-container {
    width: 80%;
    height: auto;
}

.right-container {
    width: 20%;
    height: auto;
}


.footer {
    background-color: black;
    border-top: 1px solid rgb(48, 54, 61);
    margin-top: 20px;
}

.footer-container {
    width: 95%;
    margin: auto;
}

::-webkit-scrollbar {
    width: 7px;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(28, 199, 74);
    border-radius: 8px;
}

/* ::-webkit-scrollbar-thumb:hover {
    background-color: rgb(28, 199, 74);
    border-radius: 8px;
    transition: .5s;
} */

.popup-anime
{
    transition: .5s ;
}
@media screen and (min-width: 400px) and (max-width: 900px)
{

    .content-container {
        display: block;
    }
    .left-container {
        width: 100%;
    }

    .right-container {
        width: 100%;
        margin-top: 10px;
    }
}