.carousel-container {
    width: 100%;
    height: 650px;
    border-radius: 8px;
    overflow: hidden;
}

.slider-img {
    width: 100%;
    height: 650px;
    transition: .5s ease
}

.content-desc {
    font-size: 18px;
}

.carousel-play {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    background-color: rgb(28, 199, 73);
    border-radius: 8px;
}

/* .carousel-shadow {
    background-color: #0d1117;
} */
.swiper-pagination
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    background-color: white;
    transition: .3s ease-in;
}

.swiper-pagination-bullet-active
{
    padding: 5px;
    background-color: rgb(28, 199, 73);
    width: 10px;
    height: 10px;
}
.swiper-button-prev,
.swiper-button-next

{
    width: 45px;
    height: 45px;
    background-color: rgba(255, 255, 255, 0.596);
    border-radius: 8px;
}
.swiper-button-prev:after, .swiper-button-next:after {
    font-family: swiper-icons;
    font-size: 16px;
    font-weight: bold;
    color: black;
}

.whilst {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    background-color: white;
    color: black;
    border-radius: 8px;
}


.item-category {
    width: 100%;
    height: auto;
}


.item-title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    color: #848D97;


}

.item-title span {
    font-size: 28px;
    color: rgb(28, 199, 73);
}

.item-content {
    height: 280px;
}

.item-img {
    position: relative;
    width: 100%;
    height: 80%;
    border-radius: 8px;
    overflow: hidden;
}

.item-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.btn-play {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 8px;
}

.category-item {
    padding-left: 8px;
    padding-right: 8px;
    height: 30px;
    background-color: white;
    border-radius: 8px;
    font-size: 15px;
    font-weight: bold;
}

.item-body {
    width: 100%;
    height: 20%;
    font-size: 18px;
    margin-top: 10px;
    overflow: hidden;
    color: rgb(28, 199, 73);
}


.item-content:hover .item-body {
    color: white;
    transition: 1s ease;
}

.kh-dub {
    display: flex;
    justify-content: center;
    align-items: center;
    top: 20%;
    left: 1%;
    width: 150px;
    height: 25px;
    font-family: 'Suwannaphum', serif;
    transform: translate(-29.29%, -100%) rotate(-47deg);
    background-color: #EF233C;
    color: white;
}

.subtitle {
    top: 4px;
    left: 10px;
    font-size: 30px;
    color: white;
}

/* ////////////////////////////////// */

.container {
    position: relative;
    width: 100%;
    height: 80%;
}

.image {
    width: 100%;
    height: 100%;
    transition: .5s ease;
    object-fit: cover;
}

.play-icons {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.container:hover .image {

    z-index: 1000;
    overflow: hidden;
    filter: blur(1px);
    /* background-color: #161B22; */
    /* border: 1px solid rgb(48, 54, 61); */
}

.container:hover .play-icons {
    opacity: 1;
}

.text {
    color: white;
    font-size: 16px;
    padding: 16px 32px;
}

.blur {
    filter: blur(8px);
    -webkit-filter: blur(8px);
}


@media screen and (min-width: 400px) and (max-width: 900px) {
    .carousel-container {
        height: 500px;
    }

    .slider-img {
        height: 500px;
    }

    .item-content {
        height: 165px;
    }


    .kh-dub {
        top: 35%;
        left: 1%;
        width: 150px;
        height: 25px;
    }

    .subtitle {
        font-size: 22px;
    }
}