.nav {
    width: 95%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.nav-left {
    display: flex;
    align-items: center;
}

.nav-menu-item {
    display: flex;
}

.nav-right {
    display: flex;
    align-items: center;

}

.nav-search {
    display: flex;
}

.nav-search:focus-within .search-item {
    display: block;
    transition: .2s ease;
}

.input-search {
    width: 200px;
    height: 40px;
    padding-left: 8px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    color: white;
    outline: none;
    border-right: 0px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
}

.seach-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    border-left: 0;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    font-size: 18px;

}

.icon-search {
    cursor: pointer;
}

.search-item {
    position: absolute;
    z-index: 100000;
    display: none;
    top: 70px;
    width: 240px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    border-radius: 8px;
    color: #848D97;
}

.search-title {
    font-size: 13px;
    margin-bottom: 5px;
    padding: 10px;
}

.search-item-list {
    /* border-bottom: 1px dashed rgb(48, 54, 61); */
    border-top: 1px dashed rgb(48, 54, 61);
    padding: 10px;
}

.search-item-list:hover {
    color: rgb(28, 199, 73);
    transition: .2s ease;
}

.seach-item-link {
    display: flex;
    align-items: center;
}

.seach-item-link img {
    width: 90px;
    height: 50px;
    object-fit: cover;
    border-radius: 8px;
}

.sreach-caption {
    width: 150px;
    height: 50px;
    overflow: hidden;
}

.nav-profile {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    border-radius: 8px;
    font-size: 18px;
    cursor: pointer;
}

.nav-profile:hover .profile-item {
    display: block;
}

.proifle-header {

    position: absolute;
    top: 50px;
    width: 40px;
    height: 20px;
    /* background-color: white; */
}

.profile-item {
    z-index: 100001;
    display: none;
    position: absolute;
    top: 70px;
    right: 3%;
    width: 290px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    border-radius: 8px;
    color: #848D97;
}

.profile-item-list {
    padding: 10px;
    border-top: 1px dashed rgb(48, 54, 61);
}

.profile-item-list:hover {
    color: rgb(28, 199, 73);
    transition: .2s ease;
}

.profile-infor {
    display: flex;
    justify-content: center;
    padding: 10px;
}

.profile-img {
    width: 60px;
    height: 60px;
    border: 1px solid #cccccc50;
    border-radius: 100px;
    overflow: hidden;
    object-fit: cover;
}

.profile-contact {
    display: block;
    width: 180px;
}

.profile-name {
    width: 100%;
    font-size: 17px;
}

.profile-email {
    font-size: 15px;
}

.profile-title {
    font-size: 17px;
}

.profile-icon {
    font-size: 15px;
}


.nav-mobile,
.nav-mobile-search {
    width: 40px;
    height: 40px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    color: white;
    border-radius: 8px;
    font-size: 18px;
    cursor: pointer;
    display: none;

}

.total-notifi {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: white;
    color: rgb(28, 199, 73);
    border-radius: 8px;
    font-size: 12px;
}

.nav-menu-list {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-menu-list:hover .sub-menu {
    color: rgb(28, 199, 73);
    display: block;
}

.menu-header
{
    position: absolute;
    top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 20px;
}
.sub-menu {
    display: none;
    z-index: 100000;
    position: absolute;
    top: 70px;
    width: 400px;
    background-color: #161B22;
    padding: 8px;
    border-top: 1px solid rgb(28, 199, 73);
    transition: .3s ease-in-out;

}

.sub-menu-item {
    color: white;
    display: grid;
    grid-template-columns: auto auto auto auto;
}

.sub-menu-link:hover {
    color: rgb(28, 199, 73);
    transition: .3s ease;
}

@media screen and (min-width: 400px) and (max-width: 900px) {
    .nav-menu {
        display: none;
    }

    .nav-search {
        display: none;
    }

    .nav-mobile,
    .nav-mobile-search {

        display: flex;
        justify-content: center;
        align-items: center;
    }
}