.footer-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 19px;
    height: 250px;
}

.footer-right {
    width: 20%;
    height: 100%;
}

.footer-left {
    width: 25%;
    height: 100%;
}


.footer-center {
    width: 55%;
    height: 100%;
}

.social-media-title,
.about-as {
    font-size: 20px;
    color: #848D97;
}

.social-media-item {
    display: flex;
    color: white;
}

.social-media-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    margin-top: 5px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    border-radius: 8px;
    font-size: 17px;
}

.social-media-list:hover {
    color: rgb(28, 199, 73);
    transition: .3s ease;
}

.contact-as {
    color: #848D97;
    margin-top: 15px;
}

.contact-as-title {
    font-size: 19px;
}

.contact-email,
.contact-phone {
    display: flex;
    align-items: center;
}

.about-as-body {
    font-size: 15px;
    font-family: Suwannaphum;
}

.copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0%;
    color: #848D97;
}

.copyright-link {
    color: rgb(28, 199, 73);
    font-size: 18px;
}


@media screen and (min-width: 900px) and (max-width: 1024px) {
    .footer-right {
        width: 100%;
        height: 100%;
    }

    .footer-left {
        width: 100%;
        height: 100%;
    }


    .footer-center {
        width: 100%;
        height: 100%;
    }

    .footer-item
    {
        display: block;
    }
}

@media screen and (min-width: 200px) and (max-width: 1024px) {
    .footer-right {
        width: 100%;
        height: 100%;
        margin-top: 15px;
    }
    .footer-left {
        width: 100%;
        height: 100%;
    }


    .footer-center {
        width: 100%;
        height: 100%;
        margin-top: 15px;
    }

    .footer-item
    {
        display: block;
        height: auto;
    }
}