.form-user-login {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 100vh;
    transition: .3s ease-in;
}


.header-close {
    display: flex;
    justify-content: end;
    padding: 5px;
}

.btn-close-from {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    color: white;
    border-radius: 8px;
    padding: 2px;
    cursor: pointer;
}

.header-login-logo {
    text-align: center;
}

.login-logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo {
    width: 80px;
    height: 80px;
    background-color: rgba(255, 255, 255, 0.034);
    border: 1px solid rgb(48, 54, 61);
    border-radius: 50px;

}

.popup-form {
    width: 400px;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);
    border-radius: 13px;
    color: #848D97;
    font-size: 18px;
}


.body-form {
    padding: 20px;
}

.footer-form {
    border-top: 1px dashed rgb(48, 54, 61);
    border-bottom: 1px dashed rgb(48, 54, 61);
    padding: 20px;
}

.input-form {
    width: 100%;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.034);
    border: 1px solid rgb(48, 54, 61);
    border-right: 0;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    padding-left: 10px;
    outline: none;
}

.input-style
{
    display: flex;
    align-items: center;
}
.input-style-icon
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.034);
    border: 1px solid rgb(48, 54, 61);
    border-left: 0;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
}
.login-desc {
    font-size: 16px;
}

::placeholder {
    font-size: 15px;
}

.btn-login {
    width: 100%;
    height: 40px;
    border-radius: 8px;
    background-color: rgb(28, 199, 73);
    color: white;
}

.login-whit-media {
    display: flex;
    align-items: center;
    padding: 20px;
    padding-top: 5px;
}

.btn-login-media {
    width: 100%;
    height: 40px;
    border-radius: 8px;
    background-color: white;
    color: black;
}

.forgot
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
}
