.popular-item,
.cateogry-item,
.telegram-item {
    width: 100%;
    height: auto;
    color: #848D97;
    background-color: #161B22;
    border: 1px solid rgb(48, 54, 61);

    border-radius: 8px;
}

.popular-title,
.cateogry-title,
.telegram-title {
    padding: 8px;
    background-color: black;
    border-bottom: 0;
    border-radius: 8px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    font-size: 18px;

}

.popular-all-item,
.category-all-item,
.telegram-body {
    padding: 0;
    padding-top: 8px;
    padding-bottom: 8px;
}

.popular-item-list,
.category-item-list {
    border-bottom: 1px dashed rgb(48, 54, 61);
    overflow: hidden;
}

.popular-item-link,
.category-item-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    overflow: hidden;

}

.popular-item-img {
    width: 35%;
    height: 70px;
    border-radius: 8px;
    overflow: hidden;
}

.popular-item-img img {
    width: 100%;
    height: 100%;
}

.popular-body {
    width: 75%;
}

.popular-item-title {
    width: 100%;
    height: 50px;
    overflow: hidden;
}

.popular-item-date {
    width: 100%;
    height: 50%;
    font-size: 13px;
}

.popular-item-list:hover,
.category-item-list:hover {
    color: rgb(28, 199, 73);
    transition: .5s ease;
}

.category {
    display: flex;
    align-items: center;
}

.category-item-icon {
    font-size: 18px;
    color: rgb(28, 199, 73);
}

.rated {
    color: rgb(28, 199, 73);
}


.telegram-body {
    width: 100%;
    height: 150px;
    padding: 10px;


}

.telegram {
    height: 100%;
    padding: 10px;
    background-color: #1c93e3;
    border-radius: 8px;
    color: white;
}

.telegram-desc {
    display: flex;
    align-items: center;
}

.telegram-descrip {
    font-size: 17px;
}

.telegram-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background-color: #03a9f4;
    border-radius: 8px;
    font-size: 22px;
}

.telegram-bottom {

    width: 100%;
    height: 40px;
    background-color: #FFFFFF6B;
    border-radius: 8px;
}

.btn-join {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    color: white;
    font-size: 19px;
}



@media screen and (min-width: 900px) and (max-width: 1024px) {
    .popular-item-img {
        width: 100%;
    }

    .popular-body {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }


    .popular-item-link {
        display: block;
        align-items: center;
        justify-content: space-between;
        padding: 8px;
        overflow: hidden;

    }
}


@media screen and (min-width: 1024px) and (max-width: 1366px) {
    .popular-item-img {
        width: 100%;
    }

    .popular-body {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }


    .popular-item-link {
        display: block;
        align-items: center;
        justify-content: space-between;
        padding: 8px;
        overflow: hidden;

    }
}